import React from "react";
import { Participant } from "../../../../../../../../../../model/match-entity";
// import { ParticipantEntity } from "../../../../../../models/POTG-Summoner";

function UserItemList({ user }: { user: Participant | undefined }) {
  const userItemCodes = [
    user?.item0,
    user?.item1,
    user?.item2,
    user?.item6,
    user?.item3,
    user?.item4,
    user?.item5,
  ] as number[];

  return (
    <>
      {userItemCodes.map((itemCode, index) =>
        itemCode > 0 ? (
          <img
            key={index}
            className="bg-gray-200 rounded-md overflow-hidden"
            style={{ width: "30px", height: "30px" }}
            src={`http://ddragon.leagueoflegends.com/cdn/12.15.1/img/item/${itemCode}.png`}
            alt="item"
          />
        ) : (
          <div
            key={index}
            style={{ width: "30px", height: "30px" }}
            className="bg-gray-200 rounded-md overflow-hidden"
          />
        )
      )}
    </>
  );
}

export default UserItemList;
